import React from 'react';
import Slider from "react-slick/lib";

import skill_pict from '@/img/skill_pict.png'
import skill_item_01 from '@/img/skill_item_01.jpg'
import skill_item_02 from '@/img/skill_item_02.jpg'
import skill_item_03 from '@/img/skill_item_03.jpg'
import skill_item_04 from '@/img/skill_item_04.jpg'
import skill_item_05 from '@/img/skill_item_05.jpg'

export default class Awards extends React.Component {

  render() {
    let sliderOption = {
      adaptiveHeight: false,
      autoplay: true,
      speed: 700,
      dots: false,
      arrows: true,
      slidesToShow: 3,
      slidesToScroll: 1,
      responsive: [
        {
          breakpoint: 1024,
          settings: {
            slidesToScroll: 1,
            slidesToShow: 1,
          }
        }
      ]
    };
    return (
      <Slider className="skill__slider" {...sliderOption}>
        <div className="skill__cell">
          <div className="skill__item">
            <img src={skill_item_01} alt="Почетная Грамота"/>
          </div>
        </div>
        <div className="skill__cell">
          <div className="skill__item">
            <img src={skill_pict} alt="Медаль" className="no-shadow"/>
          </div>
        </div>
        <div className="skill__cell">
          <div className="skill__item">
            <img src={skill_item_02} alt="Почетная Грамота"/>
          </div>
        </div>
        <div className="skill__cell">
          <div className="skill__item">
            <img src={skill_item_03} alt="Почетная Грамота"/>
          </div>
        </div>
        <div className="skill__cell">
          <div className="skill__item">
            <img src={skill_item_04} alt="Почетная Грамота"/>
          </div>
        </div>
        <div className="skill__cell">
          <div className="skill__item">
            <img src={skill_item_05} alt="Почетная Грамота"/>
          </div>
        </div>
      </Slider>
    )
  }

}